<template>
  <div class="lunbo">
    <!-- 首页轮播组件 -->
    <md-card>
      <md-card-media>
        <!-- swiper -->
        <swiper :options="swiperOption" v-if="pic.length">
          <swiper-slide class="swiper-slide" v-for="(p,i) in pic" :key="i">
            <img v-lazy="'http://127.0.0.1:3000'+p.pic" alt>
          </swiper-slide>
          <!-- 这个div的class应该是外部引入，无法更改 -->
          <!-- <div class="swiper-pagination" slot="pagination"></div> -->
        </swiper>
      </md-card-media>
    </md-card>
  </div>
</template>

<script>
export default {
  props: ["pic"],
  data() {
    return {
      swiperOption: {
        loop: true,
        autoplay: {
          delay: 2500,
          disableOnInteraction: false
        },
        effect: "coverflow",
        centeredSlides: true,
        slidesPerView: "auto"
      }
    };
  },
  created() {
  },
  methods: {},
  watch: {}
};
</script>

<style scoped>
.swiper-inner {
  width: 100%;
  height: 300rpx;
  padding-top: 50rpx;
  padding-bottom: 50rpx;
}
.swiper-slide {
  background-position: center;
  background-size: cover;
  width: 80%;
  height: 300rpx;
}
.swiper-slide > img {
  width: 100%;
  height: 300rpx;
}
img[lazy=loading] {
  width: 100%;
  height: 100px;
  margin: auto;
  background: url("../../../assets/register.gif") no-repeat fixed center;
}
</style>