<template>
  <div class="first1">
    <!-- <router-view /> -->
    <topnav :p="p"></topnav>
    <lunbo :pic="pic"></lunbo>
    <div class="my2_img">
      <my2 v-for="(cn,i) in data" :key="i" :cn="cn"></my2>
    </div>
    <gexing></gexing>
    <titler :pp="pp"></titler>
    <gedan :pic2="pic2"></gedan>
    <gedan :pic2="pic3"></gedan>
    <titler :pp="pp1"></titler>
    <fenlei :pic4="pic4"></fenlei>
    <titler :pp="pp2"></titler>
    <zuixin :pic5="pic5"></zuixin>
    <titler :pp="pp3"></titler>
    <vip :pic6="pic6"></vip>
    <titler :pp="pp4"></titler>
    <dujianeirong :pic7="pic7"></dujianeirong>
    <titler :pp="pp5"></titler>
    <jingxuanshipin :pic8="pic8"></jingxuanshipin>
    <titler :pp="pp6"></titler>
    <zuixin :pic5="pic9"></zuixin>
    <music></music>
    <bar></bar>
  </div>
</template>

<script>
import topnav from "./components/topnav";
import lunbo from "./components/lunbo";
import my2 from "../login/components/my2";
import gexing from "./components/gexing";
import titler from "./components/titler";
import gedan from "./components/gedan";
import fenlei from "./components/fenlei";
import zuixin from "./components/zuixin";
import vip from "./components/vip";
import dujianeirong from "./components/dujianeirong";
import jingxuanshipin from "./components/jingxuanshipin";
import music from '../music';
import bar from '../quanju';
export default {
  name:"first",
  data() {
    return {
      p: "音乐馆",
      pp:"为你推荐歌单",
      pp1:"分类专区",
      pp2:"最新专辑",
      pp3:"VIP专区",
      pp4:"独家内容",
      pp5:"精选视频",
      pp6:"乐人",
      data: [
        { img: require("../../assets/icon/geshou.png"), content: "歌手" },
        { img: require("../../assets/icon/paihang.png"), content: "排行" },
        { img: require("../../assets/icon/fenlei.png"), content: "分类歌单" },
        { img: require("../../assets/icon/diantai.png"), content: "电台" },
        { img: require("../../assets/icon/yiqiting.png"), content: "一起听" }
      ],
      // 这是轮播
      pic: [],
      // 这是推荐歌单
      pic2:[],
      // 这是推荐歌单2
      pic3:[],
      // 这是分类专区
      pic4:[],
      // 这是最新专辑
      pic5:[],
      // 这是vip专区
      pic6:[],
      // 这是独家内容
      pic7:[],
      // 这是精选视频
      pic8:[],
      // 这是乐人
      pic9:[],
    };
  },
  methods: {
    getp() {
      this.axios.get("/lunbopic").then(res => {
        var result = res.data;
        for (let i in result) {
          // 这是轮播的照片
          if (i < 6) {
            this.pic.push(result[i]);
          } else if(i<12){
            this.pic2.push(result[i]);
          }else if(i<18){
            this.pic3.push(result[i]);
          }else if(i<24){
            this.pic4.push(result[i]);
          }else if(i<27){
            this.pic5.push(result[i]);
          }else if(i<36){
            this.pic6.push(result[i]);
          }else if(i<42){
            this.pic7.push(result[i]);
          }else if(i<48){
            this.pic8.push(result[i]);
          }else if(i<51){
            this.pic9.push(result[i]);
          }else{
            return;
          }
        }
      });
    }
  },
  created() {
    this.getp();
  },
  mounted(){
  },
  components: { topnav, lunbo, my2, titler, gexing, gedan, fenlei , zuixin , vip , dujianeirong , jingxuanshipin , bar , music  }
};
</script>

<style scoped>
.first1{
  margin-bottom:6rem;
}
.my2_img {
  background: #ece8e8;
  display: flex;
  width: 100%;
  padding: 5%;
}
</style>
