<template>
  <div class="gedan">
    <div class="bottom">
      <md-card-media>
        <swiper class="bottom_img" :options="swiperOption" v-if="pic2.length">
          <swiper-slide v-for="(p,i) in pic2" :key="i">
            <img style="width:100%;" v-lazy="'http://127.0.0.1:3000'+p.pic" alt>
            <p>{{p.content}}</p>
          </swiper-slide>
          <!-- 这有bug，在lunbo中定义此div就会出错 -->
          <!-- <div class="swiper-pagination" slot="pagination"></div> -->
        </swiper>
      </md-card-media>
    </div>
  </div>
</template>

<script>
export default {
  props:["pic2"],
  data() {
    return {
      swiperOption: {
        
        slidesPerView: 3,
        spaceBetween: 30,
        freeMode: true,
        // loop: true,
        autoplay: {
          delay:3000,
          disableOnInteraction: false,
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        }
      }
    }
  }
}
}
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
}

.bottom {
  display: flex;
  width: 100%;
  padding: 0.5rem;
}
.bottom_img {
  width: 25rem;
}
.bottom_img p {
  color: #000;
  float: left;
}
.bottom_img img {
  border-radius: 0.5rem;
}
img[lazy=loading] {
  width: 100%;
  height: 100px;
  margin: auto;
  background: url("../../../assets/register.gif") no-repeat fixed center;
}
</style>
