<template>
    <div class="dujia">
      <swiper :options="swiperOptionTop" class="gallery-top" ref="swiperTop">
        <swiper-slide class="slide-1" v-for="(p,i) in pic7" :key="i">
            <img style="width:100%;" v-lazy="'http://127.0.0.1:3000'+p.pic" alt="">
            <p style="font-size:1.5rem;color:#aa0;">{{p.content}}</p>
        </swiper-slide>
        <div class="swiper-button-next swiper-button-white" slot="button-next"></div>
        <div class="swiper-button-prev swiper-button-white" slot="button-prev"></div>
      </swiper>
    </div>
</template>

<script>
export default {
    props:["pic7"],
    data() {
      return {
        swiperOptionTop: {
          spaceBetween: 10,
          loop: true, //looped slides should be the same
          navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev'
          },
        autoplay: {
          delay: 2500,
          disableOnInteraction: false
        },
        },
        effect: "coverflow",
        centeredSlides: true,
        slidesPerView: "auto"
      }
    },
    mounted() {
      this.$nextTick(() => {
        const swiperTop = this.$refs.swiperTop.swiper
        const swiperThumbs = this.$refs.swiperThumbs.swiper
        swiperTop.controller.control = swiperThumbs
        swiperThumbs.controller.control = swiperTop
      })
    }
  }
</script>

<style scoped>
img[lazy=loading] {
  width: 100%;
  height: 200px;
  margin: auto;
  background: url("../../../assets/register.gif") no-repeat fixed center;
}
</style>
