<template>
    <div class="my2">
            <div class="my2_div" @click="go(cn)">
                <img :src="cn.img" alt="">
                <p>{{cn.content}}</p>
            </div>
    </div>
</template>

<script>
export default {
    data(){
        return{
        }
    },
    props:["cn"],
    methods:{
        go(cn){
            if(cn.content=="分类歌单"){
                this.$router.push("/singer");
            }else if(cn.content=="歌手"){
                this.$router.push('/singers')
            }else if(cn.content=="一起听"){
                this.$router.push('/player/'+Math.floor(Math.random()*30))
            }else if(cn.content=="排行"){
                this.$router.push("/not")
            }
        }
    }
}
</script>

<style scoped>
.my2_div>img{
    padding:20%;
    margin-right: 1rem;
    width:100%;
}
.my2_div>p{
    color:#666;
}
</style>
